export default {
	data() {
		return {
			nav: {
				top: 0,
				height: 0
			},
			// 头像
			avator:'',
			// 用户名
			nickName:'',
		}
	},
	created() {
		// 获取胶囊信息
		const nav = uni.getMenuButtonBoundingClientRect()
		this.nav.top = nav.top
		this.nav.height = nav.height
	},
	mounted() {
		this.Init()
	},
	methods: {
		// 初始化我的页面
		Init(){
			var arr=uni.getStorageSync('token')
			this.avator=arr.avatarUrl
			this.nickName=arr.nickName
		},
		// 点击授权登录按钮
		wxGetUserInfo() {
			uni.getUserInfo({
				success: (res) => {
					this.userInfo = res.userInfo;
					console.log(this.userInfo);
					uni.setStorageSync('token',this.userInfo)
					this.Init()
				},
				fail: () => {
					console.log("未授权");
				}
			})
		},
		toUserSettings(){
			// 先要判断是否授权登录和扫码识别桌号
			var token = uni.getStorageSync('token')
			var tableNum = uni.getStorageSync('tableNum')
			if (!token) {
				uni.showToast({
					title: '请先授权登录',
					duration: 2000,
					icon: 'none',
				});
				return;
			}
			if (!tableNum) {
				uni.showToast({
					title: '请到首页扫码识别桌号',
					duration: 2000,
					icon: 'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '../index/index'
					})
				}, 2000)
				return;
			}
			uni.navigateTo({
				url:'../userSettings/userSettings'
			})
		},
		toOrder() {
			uni.navigateTo({
				url: '../evaluate/evaluate'
			})
		},
		toComment() {
			uni.navigateTo({
				url: '../allComment/allComment'
			})
		},
		toShopBag() {
			uni.navigateTo({
				url: '../shopBag/shopBag'
			})
		}
	}
}
